import { Directive,OnInit,ElementRef,Renderer2,HostListener,HostBinding,Input } from '@angular/core';

@Directive({
    selector:'[chnageBgGreen]'
})
 
export class MynewHeighlightDirective implements OnInit{
    @Input() defaultColor : string = 'transparent';
    @Input() hightLightColor : string = 'pink';
    
    @HostBinding('style.backgroundColor') mybackgroundColor : string = this.defaultColor;
    
    @HostListener('mouseenter') mouseover (){
         
        this.mybackgroundColor = this.hightLightColor;
//        this.renderer.setStyle(this.elRef.nativeElement,'background-color','yellow');
    }
    @HostListener('mouseleave') mouseleave(){
        this.mybackgroundColor = this.defaultColor;
//        this.renderer.setStyle(this.elRef.nativeElement,'background-color','transparent');
    }
    constructor(private elRef : ElementRef,private renderer : Renderer2){
        
    }
    ngOnInit(){
          this.mybackgroundColor = this.defaultColor;
//        this.renderer.setStyle(this.elRef.nativeElement,'background-color','green')
    }
}


