import { EventEmitter,Injectable } from '@angular/core';
import { Recipe } from './recipe.model';
import { Ingredient } from '../shared/ingredient.model';
import { ShoppingListService } from '../shopping-list/shopping-list.service';

@Injectable()

export class RecipeService
{
    recipeSelected = new EventEmitter<Recipe>();
    private recipes : Recipe [] = [new Recipe(' A Test another','this is simply a test',
    'http://www.pngpix.com/wp-content/uploads/2016/10/PNGPIX-COM-Fried-Fish-PNG-Transparent-Image-1.png',
    [new Ingredient('Meal1',12),
    new Ingredient('Meal2',10)
    ]),new Recipe(' A Test recipe','this is simply a test',
    'http://www.pngpix.com/wp-content/uploads/2016/10/PNGPIX-COM-Fried-Fish-PNG-Transparent-Image-1.png',
    [new Ingredient('Meal12',20),
      new Ingredient('Meal22',30)])]
     
     constructor(private shoppingListService : ShoppingListService)
     {}
     getRecipe()
     {
         return this.recipes.slice();
     }
     addIngredientsToShoppingList(ingredients : Ingredient[])
    {
        this.shoppingListService.addNewIngredients(ingredients);
    }
}


