import { Component, OnInit } from '@angular/core';
import { Ingredient } from '../shared/ingredient.model';
import { ShoppingListService } from './shopping-list.service';

@Component({
  selector: 'app-shopping-list',
  templateUrl: './shopping-list.component.html',
  styleUrls: ['./shopping-list.component.css'],
//  providers : [ShoppingListService]
})
export class ShoppingListComponent implements OnInit {
    
 parentData : string = 'data from shopping list';
 ingredients : Ingredient[];
    
// ingredients : Ingredient[] = [new Ingredient('apple',12),new Ingredient('apple',1),new Ingredient('grapes',34),new Ingredient('orange',12),new Ingredient('mango',34)]

  constructor(private shoppingListService : ShoppingListService) { }

  ngOnInit(){
        this.ingredients = this.shoppingListService.getIngredients();
        this.shoppingListService.ingredientsChanged.subscribe( (ingredient : Ingredient[])=>
        {
            this.ingredients = ingredient;                                                   
        })
      
  }
  
//  handleEvent(ingredient : Ingredient[])
//  {
//    this.ingredients.unshift(ingredient);
//  }

}



